<template>
    <form>
        <h3 class="text-muted my-5">添加新的Pizza</h3>
        <div class="form-group row">
            <label for="" class="col-sm-1">品种</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newpizza.name">
            </div>
        </div>
        <div class="form-group row">
            <label for="" class="col-sm-1">描述</label>
            <div class="col-sm-11">
                <textarea row='5' class="form-control" v-model="newpizza.description" style="resize:none"></textarea>
            </div>
        </div>
        <p><strong>选项1:</strong></p>
        </div>
        <div class="form-group row">
            <label for="" class="col-sm-1">尺寸</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newpizza.size1">
            </div>
        </div>
        <div class="form-group row">
            <label for="" class="col-sm-1">价格</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newpizza.price1">
            </div>
        </div>
        <p><strong>选项2:</strong></p>
        </div>
        <div class="form-group row">
            <label for="" class="col-sm-1">尺寸</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newpizza.size2">
            </div>
        </div>
        <div class="form-group row">
            <label for="" class="col-sm-1">价格</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newpizza.price2">
            </div>
        </div>
        <div class="form-group row">
            <button type="button" @click='addMenuItem' class="btn btn-success btn-block">添加</button>
        </div>
    </form>
</template>

<script>
    //   import axios from 'axios'
    export default {
        data() {
            return {
                newpizza: {}
            }
        },
        methods: {
            addMenuItem() {
                let data = {
                    name: this.newpizza.name,
                    description: this.newpizza.description,
                    options: [{
                            size: this.newpizza.size1,
                            price: this.newpizza.price1
                        },
                        {
                            size: this.newpizza.size2,
                            price: this.newpizza.price2
                        }
                    ]
                }
                this.axios.post('menu.json', data).then(this.$store.commit('pushdata', data))
            }
        }
    }
</script>
