<template>
  <div class="row">
    <div class="col-sm-12 col-md-8">
      <newpizza></newpizza>
    </div>
    <div class="col-sm-12 col-md-4">
      <h3 class="text-muted my-5">菜单</h3>
      <table class="table">
        <thead class="table table-default">
          <tr>
            <th>品种</th>
            <th>删除</th>
          </tr>
        </thead>
        <tbody v-for="item in getMenuItems" :key="item.name">
          <tr>
            <td>{{item.name}}</td>
            <td><button @click="deldata(item)" class="btn btn-outline-danger btn-sm ">&times;</button></td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import newpizza from '../components/NewPizza';
  export default {
    data() {
      return {
        // getMenuItems: []
      }
    },
    components: {
      newpizza
    },
    created() {
      axios.get('/menu.json').then(re => {
        const data = re.data
        const arrays = []
        for (let key in data) {
          data[key].cd = key
          let array = data[key]
          arrays.push(array)
        }
        this.$store.commit('setMenuItems', arrays)
        console.log(arrays)
        // this.getMenuItems = arrays
      })
    },
    methods: {
      deldata(item) {
        axios.delete('/menu/' + item.cd + '/.json').then(res => {
          this.$store.commit('remove',item)
        })
      }
    },
    computed: {
      getMenuItems: {
        get() {
          return this.$store.state.MenuItems
        },
        set() {}
      }
    }
    // beforeRouteLeave(to, from, next) {
