import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    MenuItems: {},
    username: null,
    idLogin: false
  },
  getters: {
    user: state => state.username,
    userid: state => state.idLogin

  },
  mutations: {
    setMenuItems(state, data) {
      state.MenuItems = data
    },
    remove(state, data) {
      state.MenuItems.forEach((item, index) => {
        if (item == data) {
          state.MenuItems.splice(index, 1)
        }
      })
    },
    pushdata(state, data) {
      state.MenuItems.push(data)
    },
    userStatus(state, user) {
      if (user) {
        state.username = user
        state.idLogin = true
      } else {
        state.username = null
        state.idLogin = false
      }
    }
  },
  actions: {
    setuser({
      commit
    }, user) {
      commit('userStatus', user)
    }
  }
})