<template>
  <div>
    <!-- <alert msg='text' class="col-sm-12">111</alert> -->
    <div class="row">
      <div class="col-sm-12 col-md-8">
        <table class="table">
          <thead class="thead-default">
            <tr>
              <th>尺寸</th>
              <th>价格</th>
              <th>加入</th>
            </tr>
          </thead>
          <tbody v-for="item in getMenuItems" :key="item.name">
            <tr>
              <td><strong>{{item.name}}</strong></td>
            </tr>
            <tr v-for="option in item.options" :key="option.size">
              <td>{{option.size}}</td>
              <td>{{option.price}}</td>
              <td><button @click="addToBasKet(item,option)" class="btn btn-sm btn-outline-success">+</button></td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="col-md-4 col-sm-12">
        <div v-if="BasKets.length > 0">
          <table class="table">
            <thead class="thead-default">
              <tr>
                <th>数量</th>
                <th>品种</th>
                <th>价格</th>
              </tr>
            </thead>
            <tbody v-for="item in BasKets" :key="item.name">
              <tr>
                <td><button @click='jian(item)' class="btn btn-sm">-</button>
                  <span>{{item.quantity}}</span>
                  <button @click='jia(item)' class="btn btn-sm">+</button></td>
                <td>{{item.name}}{{item.size}}</td>
                <td>{{item.price*item.quantity}}</td>
              </tr>
            </tbody>
          </table>
          <p>总价:{{toto +'RMB'}}</p>
          <button class="btn btn-success btn-block">提交</button>
        </div>
        <div v-else>
          {{BasKetsNo}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  // import axios from 'axios'
  export default {
    data() {
      return {
        BasKets: [],
        BasKetsNo: '购物车没有任何商品',
      }
    },
    computed: {
      toto() {
        let stu = 0
        for (let a in this.BasKets) {
          let indexid = this.BasKets[a]
          stu += indexid.quantity * indexid.price
        }
        return stu
      },
      getMenuItems(){
        return this.$store.state.MenuItems
      }
    },
    methods: {
      addToBasKet(item, option) {
        let BasKet = {
          name: item.name,
          size: option.size,
          price: option.price,
          quantity: 1
        }
        if (this.BasKets.length > 0) {
          let re = this.BasKets.filter(BasKet => {
            return BasKet.name === item.name && BasKet.price === option.price
          })
          if (re.length > 0 && this.BasKets != null) {
            re[0].quantity++
          } else {
            this.BasKets.push(BasKet)
          }
        } else {
          this.BasKets.push(BasKet)
        }
      },
      jian(item) {
        item.quantity--
          if (item.quantity <= 0) {
            this.remove(item)
          }
      },
      jia(item) {
        item.quantity++
      },
      remove(item) {
        this.BasKets.splice(this.BasKets.indexOf(item), 1)
      }
    },
    created() {
      this.axios.get('menu.json').then(res=>this.$store.commit("setMenuItems",res.data))
    },
  }
</script>
