<template>
  <div class="row mt-3">
    <div class="col-md-12 col-lg-12">
      <div class="card">
        <div class="card-body">
          <img src="../assets/0588e27996aec1f324d8266256c17fa8.png" alt="" class="mx-auto d-block" height="400px">
          <form @submit.prevent="onSubmit">
            <div class="form-group">
              <label for="email">邮箱</label>
              <input type='email' class="form-control" v-model="datac.email">
            </div>
            <div class="form-group">
              <label for="password">密码</label>
              <input type='password' class="form-control" v-model="datac.password">
            </div>
            <div class="form-group">
              <label for="password">确认密码</label>
              <input type='password' class="form-control" v-model="datac.confirmPassword">
            </div>
            <button class="btn btn-block btn-success" type="submit">注册</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        datac: {}
      }
    },
    methods: {
      onSubmit() {
        if (this.datac.password === this.datac.confirmPassword) {
          axios.post('users.json', this.datac).then(this.$router.push({
            name: 'login'
          }))
        } else {
          alert("不对")
        }
      }
    }
  }
</script>
