import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Login from './views/Login.vue'
import Register from './views/Register.vue'
import Menu from './views/Menu.vue'
import Admin from './views/Admin.vue'
import About from './views/About/About'

import Contact from './components/about/Contact'
import Delivery from './components/about/Delivery'
import hisyory from './components/about/hisyory'
import O from './components/about/O'

import Name from './components/about/Contact/Name'
import Phone from './components/about/Contact/Phone'



Vue.use(Router)
export default new Router({
  mode: 'history',
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return {
        x: 0,
        y: 0
      }
    }
  },
  base: process.env.BASE_URL,
  routes: [{
      path: '/',
      name: 'home',
      components: {
        default: Home,
        'o': O,
        'delivery': Delivery,
        'hisyory': hisyory,
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    }, {
      path: '/register',
      name: 'register',
      component: Register
    }, {
      path: '/about/about',
      name: 'about',
      component: About,
      redirect: "/about/contact",
      children: [{
          path: '/about/contact',
          name: 'contact',
          component: Contact,
          redirect: "/about/contact/name",
          children: [{
              path: '/about/contact/phone',
              name: 'phone',
              component: Phone,
            },
            {
              path: '/about/contact/name',
              name: 'name',
              component: Name,
            }
          ]
        },
        {
          path: '/about/delivery',
          name: 'delivery',
          component: Delivery,
        }, {
          path: '/about/hisyory',
          name: 'hisyory',
          component: hisyory,
        }, {
          path: '/about/o',
          name: 'o',
          component: O,
        },
      ]
    }, {
      path: '/menu',
      name: 'menu',
      component: Menu
    }, {
      path: '/admin',
      name: 'admin',
      component: Admin,
      // beforeEnter: (to, from, next) => {
      //     if (to.path == '/login' || to.path == '/register') {
      //       next();
      //     } else {
      //       alert('登陆');
      //       next('/login')
      //     }
      // }
    },
    {
      path: "*",
      redirect: "/"
    }
  ],
})