<template>
  <div class="row mt-3">
    <div class="col-md-12 col-lg-12">
      <div class="card">
        <div class="card-body">
          <img src="../assets/0588e27996aec1f324d8266256c17fa8.png" alt="" class="mx-auto d-block" height="400px">
          <form @submit.prevent="onSubmit">
            <div class="form-group">
              <label for="email">邮箱</label>
              <input type='email' class="form-control" v-model="datac.email">
            </div>
            <div class="form-group">
              <label for="password">密码</label>
              <input type='password' class="form-control" v-model="datac.password">
            </div>
            <button class="btn btn-block btn-success" type="submit">登陆</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        datac: {}
      }
    },
    methods: {
      onSubmit() {
        axios.get("users.json").then(re => {
          const data = re.data
          const users = []
          for (let a in data) {
            const user = data[a]
            users.push(user)
          }
          let result = users.filter(user => {
            return user.email === this.datac.email && user.password === this.datac.password
          })
          if (result != null && result.length > 0) {
            this.$store.dispatch('setuser', result[0].email)
            this.$router.push({
              name: 'home'
            })
          } else {
            alert("账号或者密码错误")
            this.$store.dispatch('setuser', null)
          }
        })
      }
    },
    beforeRouteEnter(to, from, next) {
      next(a => a.$store.dispatch('setuser', null))
    }
  }
</script>
